	.nav-items {
		display: flex;
		align-items: center;
		position: relative;
		width: 100%;
		height: 30px;
		background: transparent;
		border: 1px solid #76b349;
		text-decoration: none;

		& > * {
			display: flex;
			align-items: center;
			color: #e2f0d9;
			font-weight: bold;
		}
		& > :first-child {
			margin-left: 10px;
		}
		.nav-icon {
			margin-right: 10px;
			width: 8%;
		}

		&:hover {
			background: #4ca844;
			cursor: pointer;
		}
	}
	.active-link {
		background: #307e29;
		user-select: none;
		&:hover {
			background: #4ca844;
		}
	}
